.app {
  position: relative;
  text-align: center;
  height: 100vh;
  padding: 1.5rem;
}

.app__background {
  background: linear-gradient(rgba(69, 69, 69, 0.95), rgba(69, 69, 69, 0.95)), url("../images/background.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.app__links {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  margin: 0 auto;
}

@keyframes bounce {
	0%, 100%, 20%, 50%, 80% {
		transform: translateY(0)
	}
	40% {
		transform: translateY(-8px)
	}
	60% {
		transform: translateY(-3px)
	}
}
